@import "../styles/variables.scss";

.navbar-container {
  width: 100vw;
  height: 6rem;
  display: flex;
  color: white;
  align-items: center;
  justify-content: space-around;
  overflow: hidden;

  .navbar-title h1 {
    text-transform: uppercase;
    font-family: $primary-font;
    font-weight: bold;
  }

  .navbar-elements {
    width: 30rem;
    .navbar-element-container {
      display: flex;
      align-items: center;
      justify-content: center;
      justify-content: space-around;

      span {
        list-style-type: none;
        font-family: $secondary-font;
        font-size: larger;
        text-transform: capitalize;
      }

      .home {
        width: 7rem;
        height: 7rem;
        background-color: $primary-color;
        text-align: center;
        padding-top: 2.7rem;
      }
    }
  }

  .contact-section {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-family: $secondary-font;
    font-size: larger;
    .number {
      letter-spacing: 1px;
    }
    .cart-center {
      display: flex;
      align-items: center;
      color: $primary-color;
      padding-inline: 18px;
    }
    .list {
      color: $primary-color;
    }
    .button {
      width: 12rem;
      height: 3rem;
      border-radius: 4rem;
      border: none;
      color: white;
      text-transform: uppercase;
      font-family: $secondary-font;
      font-weight: bold;
      padding-inline: 0;
      letter-spacing: 1px;
      opacity: 0.85;
    }
  }

  @include media("<=tablet") {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 15px;
    .navbar-elements {
      display: none;
    }
    .contact-section {
      .number {
        display: none;
      }
      .btn-nav {
        display: none;
      }
      .cart {
        margin-right: 20px;
      }
    }
  }

  @include media(">769px") {
    .contact-section {
      .list {
        display: none;
      }
    }
  }

  @include media("<1500px") {
    .navbar-title h1 {
      font-size: 30px;
      font-weight: 800;
    }
    .navbar-elements {

      .navbar-element-container {
        font-size: 12px;
      }
    }
    .contact-section {
      .number {
        display: none;
      }
    }
  }
}
